.container{
    display: flex;
    margin: auto;
    justify-content: space-around;
    width: 45%;

}

.container button{
    height: 20px;
    background-color: rgb(226, 230, 233);
    border:none;
    border-radius: 5px;
}

.container input{
    border-radius: 5px;
    height: 30px;
    border-color: aquamarine;
}